/* eslint-disable react/jsx-props-no-spreading */
import { makeStyles } from '@material-ui/core';
import React from 'react';
import convert from 'react-attr-converter';

const useStyles = makeStyles({
  root: {
    width: '100%',
    height: '100%',

    '& > iframe': {
      width: '100%',
      height: '100%',
      minHeight: '300px'
    }
  }
});

export default function CreativeStartContent() {
  const classes = useStyles();
  const [content, setContent] = React.useState('');

  React.useEffect(() => {
    const contentPayload = new URLSearchParams(window.location.search).get('content');
    if (contentPayload) {
      setContent(window.atob(contentPayload).trim());
    }
  }, []);

  let reactContent = null;
  const isIframe = content && content.startsWith('<iframe');
  if (isIframe) {
    const parser = new DOMParser();
    const doc = parser.parseFromString(content, 'text/html');
    const iframe = doc.body.firstElementChild;
    const attrs = {};
    [...iframe.attributes].forEach((attr) => {
      attrs[convert(attr.name)] = attr.value === '' ? true : attr.value;
    });
    reactContent = <iframe title="iframe" {...attrs} />;
  }

  const isUrl = content && content.startsWith('http');
  if (isUrl) {
    reactContent = (
      <iframe
        src={content}
        title="Hello Katalon"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      />
    );
  }

  return (
    <div className={classes.root}>
      {reactContent || (
        'Welcome to Katalon Studio!'
      )}
    </div>
  );
}
